import React, {useEffect} from 'react';
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {useDispatch} from "react-redux";
import {Button, Descriptions,Card} from "antd";
import {getHospitalShowAsync, useHospitalListSelector} from "@/store/slices/hospitalList";
import {delTabs} from "@/store/slices/tabs";

const HospitalShow = () => {
    const{id} = useParams();
    const {pathname}= useLocation();
    const dispatch:any = useDispatch();
    const navigate = useNavigate();
    const {hospitalShow:{hospital,bookingRule}} = useHospitalListSelector();
    useEffect( ()=>{
        dispatch(getHospitalShowAsync(id as string));
    },[pathname])
    return (
        <>
            <Button type={"primary"} danger style={{marginBottom:10,marginLeft:20}} onClick={()=>{
                dispatch(delTabs(pathname));
                navigate("/hospital/hospitallist");
            }}>关闭并返回</Button>
            <Card>
                <Descriptions column={2} title="医院基本信息" bordered>
                    <Descriptions.Item label="医院名称">{hospital.hosname}</Descriptions.Item>
                    <Descriptions.Item label="医院LOGO">
                        {hospital.logoData && <img src={"data:image/gif;base64,"+hospital.logoData} style={{width:100}} />}
                    </Descriptions.Item>
                    <Descriptions.Item label="医院编码">{hospital.hoscode}</Descriptions.Item>
                    <Descriptions.Item label="医院地址">{hospital.param && hospital.param.fullAddress}</Descriptions.Item>
                    <Descriptions.Item label="乘车路线" span={2}>{hospital.route}</Descriptions.Item>
                    <Descriptions.Item label="医院简介" span={2}>{hospital.intro}</Descriptions.Item>
                </Descriptions>
            </Card>
            <Card style={{marginTop:10}}>
                <Descriptions column={2} title="医院预约规则信息" bordered>
                    <Descriptions.Item label="预约周期">{bookingRule.cycle}</Descriptions.Item>
                    <Descriptions.Item label="放号时间">{bookingRule.releaseTime}</Descriptions.Item>
                    <Descriptions.Item label="停号时间">{bookingRule.stopTime}</Descriptions.Item>
                    <Descriptions.Item label="退号时间">{bookingRule.quitTime}</Descriptions.Item>
                    <Descriptions.Item label="预约规则" span={2}>{bookingRule.rule}</Descriptions.Item>
                </Descriptions>
            </Card>
        </>
    );
};

export default HospitalShow;